import React from "react";
import Header from "./Header";
import { Helmet } from 'react-helmet';
import "../styles/styles.scss";

const LayoutCustom = ({ children }) => {
    return (
        <>
            <Helmet>
                <title>Appvity Website</title>
            </Helmet>
            <div className="body">
                <Header />
                <div className="main">
                    {children}
                </div>
            </div>
        </>
    );
};

export default LayoutCustom;
